.descriptionBlock {
    padding: 10px;
}
.descriptionBlock img {
    width: 100px;
    height: 100px;
}

.contact{
    padding-left: 10px;
    color: black;
    padding-bottom: 5px;
}
.forma{
    margin-bottom: 10px;
}
.forma div{
    margin-bottom: 5px;
}

.button {
    background-color: #4986cc;
    padding:0  10px 0 10px;
    margin-bottom: 7px;
    margin-top: 5px;

}

.customFileUpload {
    min-width: 100px;
    font-family: inherit;
    appearance: none;
    border: 0;
    border-radius: 5px;
    background: #4676d7;
    color: #fff;
    padding: 8px 16px;
    font-size: 1rem;
    cursor: pointer;
    margin-left: 15px;
}

.customFileUpload:hover {
    background: #1d49aa;
}

.customFileUpload:focus {
    outline: none;
    box-shadow: 0 0 0 4px #cbd6ee;
}