.postsBlock {
    padding: 10px;
}

.posts {
    margin-top: 10px;
}


.ebutton {
    min-width: 70px;
    font-family: inherit;
    appearance: none;
    border: 0;
    border-radius: 5px;
    background: #4676d7;
    color: #fff;
    padding: 4px 8px;
    font-size: 1rem;
    cursor: pointer;
    margin: 7px;
}

.ebutton:hover {
    background: #1d49aa;
}

.ebutton:focus {
    box-shadow: 0 0 0 4px #cbd6ee;
}